.wrapper {
  padding: 20px 20px;
  border: 1px solid lightgray;
  border-radius: 3px;
  -webkit-box-shadow: 5px 7px 6px 1px lightgray;
  -moz-box-shadow: 5px 7px 6px 1px lightgray;
  box-shadow: 5px 7px 6px 1px lightgray;
}

.list {
  list-style-type: none;
  padding: 10px;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.listItem {
  padding: 20px;
  list-style-position: inside;
  border: 1px solid lightgray;
  border-radius: 3px;
  -webkit-box-shadow: 3px 3px 3px 1px lightgray;
  -moz-box-shadow: 3px 3px 3px 1px lightgray;
  box-shadow: 3px 3px 3px 1px lightgray;
}

.review {
  color: gray;
  line-height: 1.33;
  padding: 5px 0;
  margin: 0;
}

.h4 {
  color: navy;
  padding: 10px 0;
  margin: 0;
}
